/* Add to cart */

.add_to_cart {
  width: 350px;
  margin: 40px 0;
  min-height: 100px;
  margin-left: 400px;
  padding: 20px;
  box-shadow: 4px 4px 8px #848486, -4px -4px 8px #fcfcfe;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #e2e2e4, #c2c2c4);
  justify-content: space-between;
}
.add_to_cart label,
.add_to_cart p {
  padding: 10px;
  margin-left: 10px;
  text-shadow: 4px 4px 8px #848486, -4px -4px 8px #fcfcfe;
}

/* Clear Cart  */
.clear-cart-button {
  background-color: rgb(161, 6, 6);
  padding: 7px 10px;
  position: relative;
  color: white;
  border-radius: 5px;
  -webkit-transform: translate(200px, -50px);
          transform: translate(200px, -50px);
  cursor: pointer;
}

.delete_order_icon {
  cursor: pointer;
  position: absolute;
  right: 5px;
  top: 0;
  font-size: 24px;
}

.my-order-list {
  position: relative;
  box-shadow: 4px 4px 8px #848486, -4px -4px 8px #fcfcfe;
  background: linear-gradient(135deg, #e2e2e4, #c2c2c4);
}

.delete_order_icon:hover {
  color: red;
}

