.my-order-list {
  width: 80%;
  margin: 40px;
  margin-left: auto;
  margin-right: 40px;
  padding: 20px;
}

.my-order-user-details {
  display: flex;
  justify-content: space-around;
  max-width: 100%;
  text-align: center;
}

.my-order-user-details p {
  font-size: 20px;
  margin: 15px 10px;
  font-weight: bold;
}

.my-order-user-details p span {
  font-size: 20px;
  font-weight: normal;
}

.cart-item-header h4 {
  margin: 0;
  padding: 0;
  border: 1px solid rgb(202, 201, 201);
  width: 33%;
}
